* {
	box-sizing: border-box;
	font-family: Montserrat, sans-serif; }

button {
	cursor: pointer; }

button:focus, .slick-slide:focus {
	outline: none; }

.blockRedLine3103 {
	z-index: 50 !important; }

.main-wrapper {
	width: 100%;
	max-width: 480px;
	margin: 0 auto;
	background: #fff;
	box-shadow: 0px 0px 35px rgba(249, 201, 186, 0.3); }

.content-wrapper {
	width: 320px;
	margin: 0 auto;
	padding: 0px 15px; }

.top {
	background-color: #FFC5CE;
	padding: 10px 0px 23px;
	position: relative;
	overflow: hidden;
	&__img {
		position: absolute;
		top: 9px;
		left: 50%;
		margin-left: -240px; }
	.content-wrapper {
		position: relative; } }

.header {
	display: flex;
	justify-content: space-between;
	margin-bottom: 20px; }

.menu {
	position: fixed;
	top: 0;
	left: 50%;
	margin-left: -240px;
	z-index: 500;
	min-height: 100vh;
	width: 480px;
	background: rgba(249, 201, 186, 0.9);
	opacity: 0;
	pointer-events: none;
	overflow-y: auto;
	transition: 0.3s;
	padding-top: 5px;
	.content-wrapper {
		position: static !important; }
	&__btn.btn {
		position: absolute;
		bottom: 14px;
		width: 290px;
		left: 50%;
		margin-left: -145px; }
	&__header {
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		margin-bottom: 60px; }
	&.menu-active {
		opacity: 1;
		pointer-events: auto; }
	&__close {
		flex: 0 0 50px;
		width: 50px;
		height: 50px;
		position: relative;
		margin: -10px -20px 0px 0px;
		cursor: pointer;
		div {
			width: 20px;
			position: absolute;
			top: 50%;
			height: 2px;
			left: 50%;
			margin-left: -10px;
			margin-top: -1px;
			background: #FF627B;
			&:nth-child(1) {
				transform: rotate(135deg); }
			&:nth-child(2) {
				transform: rotate(-135deg); } } } }

.navbar {
	li {
		margin-bottom: 30px;
		&:last-child {
			margin-bottom: 0px; }
		a {
			font-size: 24px;
			line-height: 108%;
			color: #9F624A;
			text-decoration: none;
			&:active {
				color: #FF627B;
				text-decoration: underline; }
			&:hover {
				text-decoration: underline; } } } }

.logo__name {
	flex: 1 1 auto;
	font-family: Bentham;
	text-shadow: 0px 4px 8px rgba(255, 98, 123, 0.5);
	font-size: 60px;
	line-height: 83%;
	color: #fff;
	text-transform: uppercase; }

.burger-menu {
