		width: 100%;
		height: 2px;
		background: #FFF; }
	&.menu-fixed {
		position: fixed;
		width: 50px;
		top: 10px;
		right: 50%;
		margin-right: -160px;
		z-index: 1000; }
	&.hidden {
		display: none; } }

.subtitle {
	font-size: 20px;
	line-height: 120%;
	color: #9F624A;
	width: 184px;
	margin-bottom: 35px; }

.top__prices {
	margin-bottom: 35px; }

.old-price {
	font-weight: 500;
	font-size: 25px;
	line-height: 140%;
	text-decoration-line: line-through;
	color: #FF627B;
	opacity: 0.5; }

.new-price {
	font-weight: bold;
	font-size: 50px;
	line-height: 100%;
	color: #FFFFFF;
	text-shadow: 0px 4px 8px rgba(255, 98, 123, 0.5); }

.product-img {
	max-width: 100%; }

.top__product-img {
	margin-bottom: 5px; }

.top__product-img .product-img {
	margin-left: -15px; }

.btn {
	width: 100%;
	background: #FF627B;
	border-radius: 4px;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 22px 5px;
	outline: none;
	border: none;
	text-transform: uppercase;
	font-weight: 600;
	font-size: 18px;
	line-height: 89%;
	color: #FFFFFF;
	text-decoration: none;
	transition: background 0.2s linear;
	&:active {
		background: #9C3041; }
	&:hover {
		background: #FF8196; } }

.btn-wrapper-fixed {
	position: fixed;
	bottom: 0px;
	padding: 14px 0px;
	background: rgba(255, 197, 206, 0.9);
	width: 480px;
	left: 50%;
	z-index: 400;
	opacity: 0;
	pointer-events: none;
	margin-left: -240px;
	transition: 0.3s;
	.btn {
		width: 100%;
		max-width: 290px;
		margin: 0 auto; }
	&.btn-wrapper-fixed-active {
		opacity: 1;
		pointer-events: auto; } }

.title {
	font-weight: 600;
	font-size: 32px;
	line-height: 109%; }

.content {
	font-size: 18px;
	line-height: 156%;
	color: #000000;
	opacity: 0.8; }

.innovation {
	background: #fff url(../img/innovation-bg.png) no-repeat;
	background-position: center bottom;
	padding: 60px 0px 245px;
