	&__content {
		line-height: 139%; } }

.ingredients {
	padding: 60px 0px;
	background: url(../img/ingredients-bg.png) no-repeat;
	background-position: left top;
	&__title {
		margin-bottom: 30px;
		color: #FF627B; } }



.ingredients__slider {
	overflow: hidden;
	position: relative;
	margin: 0px -5px;
	.slick-track {
		display: flex;
		align-items: flex-start;
		flex-wrap: wrap; }
	.slick-arrow {
		top: 55%;
		margin: -12.5px 0 0 -12.5px; }
	.slick-next {
		left: 55%; }
	.slick-prev {
		left: 45%; } }

.slick-arrow {
	width: 25px;
	height: 25px;
	overflow: hidden;
	position: absolute;
	z-index: 5;
	border: none;
	&::after {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 10;
		cursor: pointer;
		background-color: #F9C9BA;
		background-repeat: no-repeat;
		background-position: center center; } }
.slick-next {
	&::after {
		background-image: url(../img/arrow-next.svg); }
	&:active {
		&::after {
			background-color: #932F09;
			background-image: url(../img/arrow-next-active.svg); } } }
.slick-prev {
	&::after {
		background-image: url(../img/arrow-prev.svg); }
	&:active {
		&::after {
			background-color: #932F09;
			background-image: url(../img/arrow-prev-active.svg); } } }

.slider {
	cursor: grab; }

.ingredient {
