.input-wrapper {
	position: relative;
	margin-bottom: 18px;
	input {
		display: block;
		width: 100%;
		padding: 10px 20px;
		font-size: 16px;
		line-height: 156%;
		color: #932F09;
		background: #FFF3F5;
		border: 1px solid rgba(255, 98, 123, 0.3);
		border-radius: 5px;
		&::placeholder {
			color: #9F624A;
			opacity: 0.8; }
		&.error {
			border: 1px solid #FF002A;
			color: #FF002A; }
		&.success {
			border: 1px solid rgba(48, 172, 53, 0.7);
			color: #9F624A; }
		&:focus {
			border: 1px solid rgba(255, 98, 123, 0.7);
			outline: none; } } }

.validator__tooltip {
	left: 5px !important;
	margin-left: unset !important;
	margin-top: unset !important;
	bottom: -25px !important;
	&-inner {
		font-family: Open Sans !important;
		font-style: normal !important;
		font-weight: normal !important;
		background: #EA0000 !important;
		border-radius: 5px !important;
		font-size: 12px !important;
		line-height: 167% !important;
		color: #FFFFFF !important;
		padding: 2px 10px !important;
		&::before {
			content: '';
			position: absolute !important;
			left: 10px !important;
			top: -14px !important;
			bottom: unset !important;
			margin-left: unset !important;
			border: 7px solid transparent !important;
			border-bottom: 7px solid #EA0000 !important; } } }

